<template>
    <v-container fluid class="qiong-pt-30">
    
        <qiong-header :extra_calss="'qiong-primay-bg-title pl-7'">首購推薦</qiong-header>
        
        <qiong-panel-element>
            <div class="qiong-pt-10"></div>

            <bonus-recommend-table-header></bonus-recommend-table-header>
            <div class="qiong-pt-5"></div>
            <bonus-recommend-table :childs="childrens" v-if="childrens != null"></bonus-recommend-table>
            <loading v-else></loading>

            <div class="qiong-td">
                &nbsp;
            </div>
        </qiong-panel-element>
    </v-container>
</template>

<script>
import Loading from '../../../components/Data/Loading.vue'
import api_bonus from '../../../common/es/api_bonus/index'

import QiongHeader from '../../../components/Qiong/Pager/QiongHeader.vue'
import QiongPagePanel from '../../../components/Qiong/QiongPagePanel.vue'

import BonusRecommendTable from './Inner/BonusRecommendTable.vue'
import BonusRecommendTableHeader from './Inner/BonusRecommendTableHeader.vue'
import QiongPanelElement from '../../../components/Qiong/Ele/QiongPanelElement.vue'
import QiongSpace from '../../../components/Qiong/Ui/QiongSpace.vue'

    export default {
        components: {
            QiongHeader,
            QiongPagePanel,
                BonusRecommendTable,
                BonusRecommendTableHeader,
                Loading,
                QiongPanelElement, 
                QiongSpace  
        },
        name: '',
        data() {
            return {
                childrens: null
            }
        },
        mounted() {
            this.switchChidren()
        },
        methods: {
            switchChidren() {
                const _this = this
                const many = this.$store.state.users_enroller
                const transfer = this.$store.state.transfer
                if (many) {

                    this.childrens = [ ]
                    api_bonus._build_res(many, transfer, function (u, t) {

                        if (_this.$store.state.user_backend.unid != u.unid) {
                            _this.childrens.push(u)
                        }
                    })

                    this.childrens = this.childrens.sort(function(n, o) {
                        return n.index - o.index
                    })
                }
            }
        },
    }
</script>

<style lang="sass" scoped>
    
</style>


